import React from "react";
import { Header } from "../Elements/Header.js";
import { getGreeting } from "./generateGreeting.js";

const MainHeader = () => {

    var date = new Date().toLocaleDateString();
    var greeting = getGreeting();

  return (
    <div>
        <div>
          <h1>${greeting}</h1>
          <h3>Number of Tasks</h3>
          <Header title="Welcome back, firstName!"></Header>
        </div>
        <div class="topcorner">
            <a href="addTask.html" class="button"><span class="plusIcon"></span></a>
        </div>
        <br />
        <h2> ${date} </h2>
        <br />
    </div>
  );
};

export default MainHeader;
