export function getGreeting(){
    var greetingArray = [
        'Howdy',
        'Hi',
        'Welcome back',
        'Hola',
        'Bonjour',
        'Hey',
        'Yo',
        'Hello'
    ]
    var randomIndex = Math.floor(Math.random() * greetingArray.length); 
    var randomGreeting = greetingArray[randomIndex];
    return randomGreeting;
}