import React, { useEffect, useState } from "react";
import { createTask, getAllTasks } from "../../Services/LearnService.js";

var postCheck = false;

const MainList = () => {
  const [tasks, setTasks] = useState([]);

  if (!postCheck) {
    createTask(1, "Create Task", "At home", "3/26/2021", "2:00pm", "Biweekly", 1);
    postCheck = true;
  }

  useEffect(() => {
    getAllTasks().then((tasks) => {
      setTasks(tasks);
    });
  }, []);

  console.log(tasks);
  return (
    <div>
      <hr />
      This is the main list component.
      {tasks.length > 0 && (
        <ul>
          {tasks.map((task) => (
            <li key={task} class="test">
              {task.title} | {task.location} | {task.time}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default MainList;
