// import React from "react";
import axios from "axios";

//const url = "../data/taskData.json";
const url = "../data/taskData.json";

export const createTask = (id, title, location, date, time, frequency, dayTotal) => {
  return axios({
    method: "post",
    url: `${url}`,
    data: {
      id,
      title,
      location,
      date,
      time,
      frequency,
      dayTotal
    },
    headers: {
      "Content-Type": "application/json"
    },
    json: true
  })
    .then((response) => {
      console.log("POST response: ", response);
    })
    .catch((err) => {
      console.log("POST error: ", err);
    });
};

export const getAllTasks = () => {
  return axios
    .get(`${url}`)
    .then((response) => {
      console.log(response.data);
      return response.data;
    })
    .catch((err) => {
      console.log("GET Error: ", err);
    });
};
